/* @import url('../styles/App.scss'); */
@import url('../src/App.css');
body {
    font-family: 'Poppins', sans-serif;
    color: #343434;
    font-weight: 400;
  }
  
  .section-padding {
    padding: 100px 0;
  }
  
  .section-head {
    margin-bottom: 80px;
    text-align: center;
  }
  .section-head h3 {
    font-weight: 900;
    font-size: 40px;
    position: relative;
    display: inline-block;
    z-index: 4;
  }
  .section-head h3:after {
    content: '';
    width: 100%;
    height: 30px;
    position: absolute;
    bottom: -10px;
    left: 0;
    z-index: -1;
  }
  
  .bg-gray {
    background: #f7f7f7;
  }
  
  h1, h2, h3, h4, h5, h6, .logo {
    font-family: 'Montserrat', sans-serif;
  }
  
  p {
    line-height: 1.6;
    margin: 0;
    font-size: 15px;
    color: #748182;
  }
  
  .cd-headline {
    font-size: 3rem;
    line-height: 1.2;
  }
  
  @media only screen and (min-width: 768px) {
    .cd-headline {
      font-size: 4.4rem;
      font-weight: 300;
    }
  }
  @media only screen and (min-width: 1170px) {
    .cd-headline {
      font-size: 6rem;
    }
  }
  .cd-words-wrapper {
    display: inline-block;
    position: relative;
    text-align: left;
  }
  
  .cd-words-wrapper b {
    display: inline-block;
    position: absolute;
    white-space: nowrap;
    left: 0;
    top: 0;
  }
  
  .cd-words-wrapper b.is-visible {
    position: relative;
  }
  
  .no-js .cd-words-wrapper b {
    opacity: 0;
  }
  
  .no-js .cd-words-wrapper b.is-visible {
    opacity: 1;
  }
  
  /* xclip */
  .cd-headline.clip span {
    display: inline-block;
    padding: .2em 0;
  }
  
  .cd-headline.clip .cd-words-wrapper {
    overflow: hidden;
    vertical-align: top;
  }
  
  .cd-headline.clip .cd-words-wrapper::after {
    /* line */
    content: '';
    position: absolute;
    top: 10%;
    right: 0;
    width: 2px;
    height: 70%;
    background-color: #aebcb9;
  }
  
  .cd-headline.clip b {
    opacity: 0;
  }
  
  .cd-headline b.is-visible {
    opacity: 1;
  }
  
  .buton {
    padding: 10px 30px;
    border: 1px solid #1c1c1c;
    border-radius: 5px;
    font-size: 13px;
    -webkit-transition: all .5s;
    transition: all .5s;
  }
  .buton:hover {
    background: #1c1c1c;
    color: #eee;
  }
  
  .buton-bg {
    background: #1c1c1c;
    color: #eee;
  }
  .buton-bg:hover {
    background: transparent !important;
    color: #343434 !important;
  }
  
  .appWhatsapp {
    position: fixed;
    right: 26px;
    bottom: 118px;
    width: 60px;
    z-index: 1000;
  }
  
  .appWhatsapp img{
    width: 100%;
    height: auto;
  }