@import url('../src/index.css');

.appWhatsapp {
  position: fixed;
  right: 26px;
  bottom: 118px;
  width: 60px;
  z-index: 1000;
}

appWhatsapp img{
  width: 100%;
  height: auto;
}

.header {
  height: 100vh;
  background-size: cover;
  position: relative;
  overflow: hidden;
}
.header:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #000;
  opacity: .65;
  z-index: 2;
}
.header .v-middle {
  z-index: 11;
}
.header .caption {
  text-align: center;
  color: #fff;
  margin-top: 50px;
}
.header .caption h5 {
  font-size: 50px;
  font-weight: 800;
  margin-bottom: 10px;
}
.header .caption h1 {
  font-weight: 200;
}
.header .caption h1 b {
  font-weight: 400;
}
.header .caption .social-icon {
  margin-top: 30px;
}
.header .caption .social-icon span {
  font-size: 14px;
  width: 35px;
  height: 35px;
  line-height: 35px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.3);
}

.left-svg{
    position: absolute;
    bottom: 0;
    left: 0;
    background: none;
    z-index: 99;
}

.right-svg{
    position: absolute;
    bottom: 0;
    right: 0 !important;
    background: none;
    z-index: 99;
    left: auto;
}

.left-svg path, .right-svg path {
    fill: #fff;
    stroke: #fff;
    stroke-width: 4;
}

.white-bg .left-svg path, .white-bg .right-svg path {
    stroke: #f2f2f2;
    fill: #f2f2f2;
}


.float{
  position:fixed;
  width:60px;
  height:60px;
  bottom:130px; /* posicion del icono de WhatsApp*/
  right:40px;
  background-color:#25d366;
  color:#FFF;
  border-radius:50px;
  text-align:center;
  font-size:30px;
  box-shadow: 2px 2px 3px #999;
  z-index:100;
}

.my-float{
  margin-top:16px;
}


